.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh; /* Use min-height to allow for scrolling if needed */
  text-align: center;
  background-color: #012265;
  overflow: auto; /* Make the page scrollable if content overflows */
}

.input-row {
  display: flex;
  justify-content: center;
  flex-wrap: wrap; /* Allow the row to wrap to new lines if needed */
  margin-bottom: 20px;
}

.input-container {
  margin: 10px;
  padding: 20px; /* Increase padding for better spacing */
  border: 1px solid #ddd;
  border-radius: 5px;
  background-color: #d4af37;
  width: 400px; /* Increase width for more space */
  text-align: center;
}

.input-box {
  padding: 10px;
  margin-bottom: 10px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  width: 360px; /* Adjust width to fit within the input container */
  text-align: center;
  background-color: white;
  color: black; /* Ensure the text is visible */
}

.input-box::placeholder {
  color: #999; /* Change placeholder color to be more visible */
  opacity: 0.7;
}

.top-input {
  background-color: #0A3D91;
}

.bottom-input {
  background-color: #B22222;
}

.team-data-container {
  text-align: center;
  background-color: #d4af37;
  border-radius: 5px;
  padding: 20px; /* Increase padding for better spacing */
  margin-top: 10px;
}

.team-header {
  font-weight: bold;
  margin-bottom: 10px;
}

.section-header {
  font-weight: bold;
  margin-top: 10px;
  margin-bottom: 5px;
}

.grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr; /* Two columns */
  gap: 10px; /* Increase gap for better spacing */
}

.grid-item {
  border: 1px solid #ddd;
  padding: 10px; /* Increase padding for better visibility */
  background-color: #f2f2f2;
}
